import React from "react";
import { connect } from "react-redux";

const Header = props => (
    <header>
        <a href="~/"><img src={"~/images/" + props.skin + "/moo@2x.png"} alt={props.appName} className="desktop" width="76" height="111" /><img src={"~/images/" + props.Skin + "/moo-small@2x.png"} alt={props.appName} className="img mobile" width="99" height="32" /></a>
        <nav>
            @if (User.Identity.IsAuthenticated)
      {
                <ul>
                    <li><a href="~/">Home</a></li>
                    <li className="desktop"><a href="~/virtualaccounts">Manage Accounts</a></li>
                    <li className="desktop"><a href="~/home/settings">Settings</a></li>
                    <li><a href="~/login/logout">Logout</a></li>
                </ul>
            }
        </nav>
    </header>);

export default connect()(Header);